h2 {
  padding: 3rem 0 3rem 0;
  text-align: center;
  letter-spacing: 0.15rem;
}

i {
  font-size: 3rem !important;
  padding-bottom: 2rem;
  -webkit-animation: jump 0.6s linear alternate infinite;
          animation: jump 0.6s linear alternate infinite;
  opacity: 0.3;
}

.progressBar {
  position: absolute;
  left: 25%;
  width: 50vw;
  top: 30%;
}

@-webkit-keyframes jump {
  0%   {-webkit-transform: translate3d(0,0,0);transform: translate3d(0,0,0);}
  40%  {-webkit-transform: translate3d(0,5%,0);transform: translate3d(0,5%,0);}
  100% {-webkit-transform: translate3d(0,30%,0);transform: translate3d(0,30%,0);}
}

@keyframes jump {
  0%   {-webkit-transform: translate3d(0,0,0);transform: translate3d(0,0,0);}
  40%  {-webkit-transform: translate3d(0,5%,0);transform: translate3d(0,5%,0);}
  100% {-webkit-transform: translate3d(0,30%,0);transform: translate3d(0,30%,0);}
}



